
.skip {
    text-align: right;
    color: #9FB2FF;
    text-decoration: underline;
    margin-bottom: 20px;
}

img {
    width: 100%;
}

.data h3 {
    margin-bottom: 10px;
}

.data p {
    margin-bottom: 20px;
    color: #787878;
}

.buttonpage {
    text-align: right;
}

.buttonpage button {
    background: linear-gradient(to right, #ccd3f0, #89A1FF);
    border: transparent;
    border-radius: 3rem;
    color: #fff;
    cursor: pointer;
    padding: 1.5rem;
    margin-top: 3rem;
    display: inline-flex;
    align-items: center;
}

.buttonpage button:focus {
    outline: none;
}

.loader {
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top: 4px solid #fff;
  width: 24px;
  height: 24px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
